html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 3.8rem;
}
@media (min-width: 768px) {
  h1 {
    font-size: 5rem;
  }
}

h2 {
  font-size: 3.2rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 4rem;
  }
}

h3 {
  font-size: 2.4rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 3rem;
  }
}

a {
  text-decoration: none;
  color: #000;
}

fieldset,
button {
  border: none;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

input {
  border: none;
  outline: none;
}

.text-center {
  text-align: center;
}

[class$=contenedor] {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

.mt-10 {
  margin-top: 10rem;
}
@media (min-width: 768px) {
  .mt-10 {
    margin-top: 0;
  }
}

.calculator {
  height: 100vh;
  background-color: hsl(222deg, 26%, 31%);
  padding: 2rem;
  color: hsl(0deg, 0%, 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: all 200ms ease-in-out;
  /* Main background-color */
  /* Text color */
}
.calculator.darkTheme--main {
  background-color: hsl(222deg, 26%, 31%);
}
.calculator.lightTheme--main {
  background-color: hsl(0deg, 0%, 90%);
}
.calculator.violetTheme--main {
  background-color: hsl(268deg, 75%, 9%);
}
.calculator.darkTheme--text {
  color: hsl(0deg, 0%, 100%);
}
.calculator.lightTheme--text {
  color: hsl(60deg, 10%, 19%);
}
.calculator.violetTheme--text {
  color: hsl(52deg, 100%, 62%);
}
.calculator__container {
  width: 90%;
  max-width: 40rem;
  margin: 0 auto;
}
.calculator__header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0rem;
  align-items: end;
}
.calculator__heading {
  margin: 0;
  font-size: 2.8rem;
}

.themes {
  display: flex;
  align-items: flex-end;
  justify-content: space-evenly;
  margin-left: 4rem;
}
.themes__label {
  text-transform: uppercase;
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
  font-weight: 900;
  letter-spacing: 0.1rem;
}
.themes__container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
}
.themes__numbers {
  display: flex;
  width: 70%;
  font-size: 1.2rem;
  margin: 0 0.8rem -0.2rem 0;
  justify-content: space-between;
  align-items: center;
}

.slide {
  -webkit-appearance: none;
  width: 80%;
  height: 3rem;
  border-radius: 2rem;
  background: hsl(223deg, 31%, 20%);
  outline: none;
  opacity: 0.8;
  cursor: pointer;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}
.slide:hover {
  opacity: 1;
}
.slide.darkTheme--slide {
  background: hsl(223deg, 31%, 20%);
}
.slide.lightTheme--slide {
  background-color: hsl(0deg, 5%, 81%);
}
.slide.viotelTheme--slide {
  background-color: hsl(268deg, 71%, 12%);
}
.slide::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 0px 11px 8px rgba(0, 0, 0, 0.26);
  background-color: var(--colorAfter, hsl(6deg, 63%, 50%));
  background-size: 45%;
  background-position: center;
  background-repeat: no-repeat;
}
.slide::-moz-range-thumb {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  cursor: pointer;
  box-shadow: 0px 0px 11px 8px rgba(0, 0, 0, 0.26);
  background-color: hsl(6deg, 63%, 50%);
  background-size: 45%;
  background-position: center;
  background-repeat: no-repeat;
}

.screen {
  margin-top: 3rem;
  padding: 1rem;
  font-size: 2.5rem;
  font-weight: 700;
  background-color: hsl(224deg, 36%, 15%);
  color: hsl(0deg, 0%, 100%);
  border-radius: 1rem;
  text-align: right;
  line-height: 1;
  height: 8rem;
  display: flex;
  overflow: hidden;
  flex-direction: column-reverse;
  justify-content: center;
}
.screen__valorActual {
  font-size: 2rem;
  margin-bottom: 1rem;
  margin: 0.5rem 0.5rem 1rem;
}
.screen.darkTheme--screen {
  background-color: hsl(224deg, 36%, 15%);
}
.screen.lightTheme--screen {
  background-color: hsl(0deg, 0%, 93%);
}
.screen.viotelTheme--screen {
  background-color: hsl(268deg, 71%, 12%);
}
.screen.theme1--color {
  color: hsl(0deg, 0%, 100%);
}
.screen.theme2--color {
  color: hsl(60deg, 10%, 19%);
}
.screen.theme3--color {
  color: hsl(52deg, 100%, 62%);
}

.keyPad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5rem;
  margin-top: 2.5rem;
  background-color: hsl(223deg, 31%, 20%);
  padding: 2.5rem;
  border-radius: 1rem;
}

.keyPad__numbers, .reset,
.delete, .result,
.btn-operator {
  height: 6.3rem;
  font-size: 2.5rem;
  font-weight: 700;
  border-radius: 1rem;
  background-color: hsl(30deg, 25%, 89%);
  box-shadow: 0px 3px 0px 0px hsl(28deg, 16%, 65%);
  color: hsl(221deg, 14%, 31%);
  transition: 100ms ease-in-out background-color;
  cursor: pointer;
}
.keyPad__numbers:active, .reset:active,
.delete:active, .result:active,
.btn-operator:active {
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0px 2px 0px 0px hsl(28deg, 16%, 65%);
}

.delete,
.reset {
  font-size: 1.8rem;
  background-color: hsl(225deg, 21%, 49%);
  color: hsl(0deg, 0%, 100%);
  box-shadow: 0px 3px 0px 0px hsl(224deg, 28%, 35%);
}
.delete:active,
.reset:active {
  background-color: hsl(225deg, 29%, 58%);
  box-shadow: 0px 2px 0px 0px hsl(224deg, 28%, 35%);
}
.delete.lightThemeDelReset,
.reset.lightThemeDelReset {
  background-color: hsl(185deg, 42%, 37%);
  box-shadow: 0px 3px 0px 0px hsl(185deg, 58%, 25%);
}
.delete.lightThemeDelReset:active,
.reset.lightThemeDelReset:active {
  background-color: hsl(185deg, 41%, 44%);
  box-shadow: 0px 2px 0px 0px hsl(224deg, 28%, 35%);
}
.delete.violetThemeDelReset,
.reset.violetThemeDelReset {
  background-color: hsl(281deg, 89%, 26%);
  box-shadow: 0px 3px 0px 0px hsl(285deg, 91%, 52%);
  color: hsl(0deg, 0%, 100%);
}
.delete.violetThemeDelReset:active,
.reset.violetThemeDelReset:active {
  background-color: hsl(281deg, 72%, 35%);
  box-shadow: 0px 2px 0px 0px hsl(285deg, 91%, 52%);
}

.reset {
  grid-column: 1/3;
}

.result {
  grid-column: 3/5;
  background-color: hsl(6deg, 63%, 50%);
  color: hsl(0deg, 0%, 100%);
  box-shadow: 0px 3px 0px 0px hsl(6deg, 70%, 34%);
}
.result:active {
  background-color: hsl(6deg, 79%, 57%);
  box-shadow: 0px 2px 0px 0px hsl(6deg, 70%, 34%);
}

/***** THEMES *****/
/* KeyPad*/
.darkTheme--keyPad {
  background-color: hsl(223deg, 31%, 20%);
}

.lightTheme--keyPad {
  background-color: hsl(0deg, 5%, 81%);
}

.violetTheme--keyPad {
  background-color: hsl(268deg, 71%, 12%);
}

/* Numbers */
.DarkTheme--number {
  background-color: hsl(30deg, 25%, 89%);
  box-shadow: 0px 3px 0px 0px hsl(28deg, 16%, 65%);
}
.DarkTheme--number:active {
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0px 2px 0px 0px hsl(28deg, 16%, 65%);
}

.lightTheme--number {
  background-color: hsl(45deg, 7%, 89%);
  box-shadow: 0px 3px 0px 0px hsl(35deg, 11%, 61%);
}
.lightTheme--number:active {
  background-color: hsl(0deg, 0%, 100%);
  box-shadow: 0px 2px 0px 0px hsl(35deg, 11%, 61%);
}

.violetTheme--number {
  background-color: hsl(268deg, 47%, 21%);
  box-shadow: 0px 3px 0px 0px hsl(290deg, 70%, 36%);
  color: hsl(52deg, 100%, 62%);
}
.violetTheme--number:active {
  background-color: hsl(268deg, 43%, 30%);
  box-shadow: 0px 2px 0px 0px hsl(290deg, 70%, 36%);
}

/* result */
.darkTheme--result {
  background-color: hsl(6deg, 63%, 50%);
  box-shadow: 0px 3px 0px 0px hsl(6deg, 70%, 34%);
}
.darkTheme--result:active {
  background-color: hsl(6deg, 79%, 57%);
  box-shadow: 0px 2px 0px 0px hsl(6deg, 70%, 34%);
}

.lightTheme--result {
  background-color: hsl(25deg, 98%, 40%);
  box-shadow: 0px 3px 0px 0px hsl(25deg, 99%, 27%);
}
.lightTheme--result:active {
  background-color: hsl(25deg, 94%, 45%);
  box-shadow: 0px 2px 0px 0px hsl(25deg, 99%, 27%);
}

.violetTheme--result {
  background-color: hsl(176deg, 100%, 44%);
  box-shadow: 0px 3px 0px 0px hsl(177deg, 92%, 70%);
  color: hsl(198deg, 20%, 13%);
}
.violetTheme--result:active {
  background-color: hsl(176deg, 100%, 56%);
  box-shadow: 0px 2px 0px 0px hsl(177deg, 92%, 70%);
}

/* Delete and reset */
.darkThemeDelReset {
  background-color: hsl(225deg, 21%, 49%);
  box-shadow: 0px 3px 0px 0px hsl(224deg, 28%, 35%);
}
.darkThemeDelReset:active {
  background-color: hsl(225deg, 29%, 58%);
  box-shadow: 0px 2px 0px 0px hsl(224deg, 28%, 35%);
}